<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <!-- v-for directive needs a unique key, the firebase id here, to iterate -->
      <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
        <div class="chip"> {{employee.dept}} </div> {{employee.employee_id}}:{{employee.name}}
        <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
    
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      employees: []
    };
  },
  // will run when created:
  created() {
    // docs at https://firebase.google.com/docs/firestore/query-data/get-data
    db
      .collection("employees")
      .orderBy('dept')
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          // in the doc's prototype, we want the data function
          // console.log(doc.data());
          // or get the firebase id with doc.id
          const data = {
            id: doc.id, //firebase id
            employee_id: doc.data().employee_id, //out own id
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>



